import React, {useState} from 'react';
import {AiOutlineMinusCircle, AiOutlinePlusCircle} from 'react-icons/ai';


const AccordionItem = (props) => {

    const [showAnswer, setShowAnswer] = useState(false);
    function show(){
       setShowAnswer(!showAnswer)
    };

    return(
        <div className='single-question' onClick={show} data-aos="flip-left">
            <div className={showAnswer ? 'the-question line-cont' : 'the-question'}>
                <h6>{props.question}</h6>
                <div className='icon' >
                    {showAnswer===false ? <AiOutlinePlusCircle /> : <AiOutlineMinusCircle />}
                </div>
            </div>
            <div className='the-answer' style={{display: showAnswer ? 'block' : 'none'}}>
                <div className='line'></div>
                <p>{props.answer}</p>
            </div>
        </div>
    )
}

export default AccordionItem